@import "../../../assets//styles/base";
.Toolbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  top: 0;
  left: 0;
  padding: 5px 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  z-index: 90;
  vertical-align: middle;
  font-size: $base_Header_Size;
  @include tablet {
    font-size: inherit;
  }
 
  @include mobile {
    font-size: inherit ;
  }
  
    @media (min-width: #{$tablet-width+1}) and (max-width: #{$desktop-width})  {
     
      font-size:$base_Header_Size * 0.75;
   
    }
  
}
.mediaQuery {
  visibility: hidden;
}
.rightSideOftoolBar {
  position: relative;
  vertical-align: "middle";
  color: "green";
  margin: " auto 0 ";
  padding-right: "80px";
  @include mobileOnly{
    display: none;
  }
  
}
.leftSideOftoolBar{
  align-self: "flex-start";
  @include mobileOnly{
    display: none;
  }
}